
import React,  {useEffect, useState}  from 'react';
import {useSelector} from 'react-redux';
import {BrowserRouter, Switch, Route, NavLink} from 'react-router-dom';
import {ItemList} from './component-itemsList/ItemList.jsx';
import {Item} from './component-item/Item';
import {store} from './store/store.js';
import {Movie} from './Movie.jsx';
import {Pagination} from 'antd';

const pathes = {
	app: '/',
	item: '/item'
  };
  
export const App = () => {

		const [movies, setMovie] = useState([]);
		useEffect(() => {
		  fetch('https://api.themoviedb.org/3/movie/popular?api_key=9b47375a58b820da3e1293213c50ac24&language=en-US&page=1')
			.then((res) => res.json())
			.then((data) => {
			  setMovie(data.results);
			  console.log(data.results);
			});
		  return () => {};
		}, []);

//   const {arr} = movie;
//   if (!arr) {
//     return (
//       <div>
//         Loading Loading
//       </div>
//     );
//   }
const {title, poster_path, overview, genre_ids, vote_average} = movies;
return (
    <> 
     <div>
		 
         {movies.length > 0 && movies.map(movie => (
             <Movie data={movie} key={movie.id} {...movie} />
          ))}
		  <Pagination defaultCurrent={1} total={50} pageSize={10} />
    </div>
    </>
  );

};